import React, { useState, useEffect, useRef } from 'react';

const OptimizedImage = ({ 
  src, 
  alt, 
  className = '', 
  width, 
  height, 
  priority = false,
  onError 
}) => {  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(false);
  const imgRef = useRef(null);
  const [dimensions, setDimensions] = useState({ 
    width: width || 0, 
    height: height || 0 
  });

  // Early load image dimensions if not provided
  useEffect(() => {
    if (!width || !height) {
      const img = new Image();
      img.src = src;
      
      // Use native browser lazy loading hint
      if ('loading' in HTMLImageElement.prototype) {
        img.loading = 'lazy';
      }
        img.onload = () => {
        setDimensions({ 
          width: img.naturalWidth, 
          height: img.naturalHeight 
        });
        setIsLoading(false);
      };
      
      img.onerror = () => {
        setError(true);
        setIsLoading(false);
      };
    } else {
      setDimensions({ width, height });
    }
  }, [src, width, height]);
  // Extract Tailwind dimensions
  const hasTailwindDimensions = /w-\d+|h-\d+/.test(className);

  // Generate blur placeholder
  const placeholder = `data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 ${width} ${height}'%3E%3Crect width='100%25' height='100%25' fill='%23f3f4f6'/%3E%3C/svg%3E`;

  return (
    <div 
      className={`relative overflow-hidden ${hasTailwindDimensions ? '' : 'w-full'} ${className}`}
      style={{ 
        width: width ? `${width}px` : '100%',
        aspectRatio: dimensions.width && dimensions.height ? 
          `${dimensions.width}/${dimensions.height}` : '16/9',
        contain: 'layout paint style'
      }}
    >
      {isLoading && !error && (
        <div className="absolute inset-0 bg-gray-100 animate-pulse" />
      )}
      <img
        ref={imgRef}
        src={error ? '/eggpic.webp' : src}
        alt={alt}
        width={dimensions.width || undefined}
        height={dimensions.height || undefined}
        loading={priority ? "eager" : "lazy"}
        decoding="async"        onLoad={() => {
          setIsLoading(false);
          // Force layout recalculation
          if (imgRef.current) {
            imgRef.current.style.transform = 'translateZ(0)';
          }
        }}
        onError={(e) => {
          setError(true);
          setIsLoading(false);
          e.target.onerror = null;
          e.target.src = '/logo.png';
          if (onError) {
            onError(e);
          }
        }}
        className={`absolute top-0 left-0 w-full h-full object-cover transition-opacity duration-300 ${
          isLoading ? 'opacity-0' : 'opacity-100'
        }`}
        style={{ 
          backgroundImage: `url(${placeholder})`,
          backgroundSize: 'cover',
          contain: 'layout paint style',
          willChange: 'transform',
          transform: 'translateZ(0)'
        }}
      />
      {error && (
        <div className="absolute top-0 left-0 w-full h-full flex items-center justify-center text-gray-500">
          <span>Image not available</span>
        </div>
      )}
    </div>
  );
};

export default OptimizedImage;
